@import "./assets/styles/chart.css";

/* MAIN STYLES */

div,
header,
footer,
ul,
h1,
h3,
h4,
p {
    margin: 0;
    padding: 0;
}

#map {
    width: 100%;
    height: 60vh;
}

ul {
    list-style-type: none;
}

a {
    color: #FFF;
    text-decoration: none;
}

body {
    font-family: 'Poiret One', cursive;
    font-size: 18px;
    line-height: 1.3;
}

.wrap {
    /* height: 100vh;
  min-height: 768px; */
}


/* HEADER STYLES */

header {
    display: flex;
    align-items: center;
    padding: 20px 0;
    border-bottom: 6px solid #8B0000;
    background-color: #000;
    color: #FFF;
}

header div {
    width: 50%;
}

h1 {
    padding-left: 20px;
}

h1 span {
    color: #8B0000;
}

.update-date {
    text-align: right;
}

.update-date p {
    padding-right: 20px;
}


/* DATA STYLES */

.wrap-data {
    display: flex;
}

.col-left {
    width: 20%;
}

.col-right {
    width: 30%;
}

.col-center {
    width: 50%;
}

h3 {
    margin: 2px;
    margin-bottom: 0;
    padding: 10px;
    background-color: #000;
    color: #FFF;
    letter-spacing: 3px;
}

.global p {
    margin: 0 2px 2px;
    padding: 0;
    background-color: #000;
}

.global .note {
    font-size: 10px;
}

.widget {
    position: relative;
    padding: 5px;
}

button.icon-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}

button.open {
    position: absolute;
    top: 5px;
    right: 5px;
}

button.icon-menu:hover {
    background-color: #8B0000;
    color: white;
    transition: all .3s;
}

button.icon-menu:focus {
    outline: none;
}

.prev {
    margin-right: 10px;
}

.global>div {
    margin-bottom: 5px;
    padding: 5px;
    background-color: #272727;
    border-radius: 10px;
}

div.toggle,
div.toggle-data-country {
    display: flex;
    justify-content: center;
    margin: 0 2px 5px;
    background-color: #000;
    border-radius: 0 0 10px 10px;
}

div.toggle-data-country {
    margin: 0;
    padding: 5px 0;
}

.title-toggle {
    padding: 0 20px 0 10px;
    color: #FFF;
}

div.toggle-country {
    display: flex;
    justify-content: center;
    margin: 2px 2px 5px;
    padding: 10px;
    background-color: #000;
    border-radius: 0 0 10px 10px;
}

.toggle-country input {
    width: 80%;
}

.toggle-country input:focus {
    outline: none;
}

h4 {
    padding: 10px;
    color: #FFF;
    letter-spacing: 2px;
}

.col-left p {
    padding: 10px 0;
    text-align: center;
    font-size: 26px;
    color: #FFF;
    letter-spacing: 3px;
}

.countries {
    margin-bottom: 5px;
    padding: 15px 0;
    background-color: #272727;
    height: 200px;
    overflow-y: scroll;
}

.item-country {
    padding: 5px 10px;
    color: #FFF;
    cursor: pointer;
}

.item-country:nth-child(2n) {
    background-color: #000;
}

.item-country:hover {
    /* color: orange; */
    background-color: #8B0000;
}

.item-country span {
    padding-left: 20px;
    letter-spacing: 2px;
}

.window-hidden {
    display: none;
}

div.full-width {
    display: block;
    width: 100%;
}


/* FOOTER STYLES */

footer {
    padding: 20px 0;
    background-color: #8B0000;
    color: #FFF;
}

footer .links {
    display: flex;
    justify-content: center;
}

.links li {
    margin-right: 20px;
}

.links li:last-child {
    margin-right: 0;
}

.links li:hover a {
    color: orange;
    transition: color .2s;
}

footer p {
    width: 70px;
    margin: auto;
}

footer img {
    max-width: 100%;
}


